<template>
    <div table-responsive>
        <table class="table table-sm table-hover">
            <thead class="thead-dark">
                <tr>
                    <th scope="col">성명</th>
                    <th scope="col">메일</th>
                    <th scope="col">관리자권한</th>
                    <th scope="col"> </th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="user in this.users.filter((cuser)=> !!cuser.uid)" :key="user.uid">
                    <td scope="row">{{user.username}}</td>
                    <td scope="row">{{user.email}}</td>
                    <td scope="row"><input type="checkbox" v-model="user.isChecked" @change="onChange(user)"></td>
                    <td scope="row"><b-button pill variant="danger" size="sm" @click="myDeleteUser(user)">삭제</b-button></td>
                </tr>
            </tbody>
        </table>
    </div>
    <div class="footer">
        <div class="d-flex justify-content-center">
            <b-button class="btn" variant="primary" to="/home" size="lg" >홈</b-button>
        </div>
        <!-- <div class="d-flex justify-content-center">
            <button class="btn btn-primary" to="/home" size="lg" >확 인</button>
        </div> -->
    </div>    
</template>

<script>
import {ref} from 'vue';
const in_users=ref([]);

export default{
    name: "UserInfo",
    data() {
        return{
            users:in_users,
        }        
    },    
    mounted() {
        this.$store.dispatch("fetchAlluserList");
    },
    computed: {
        // getBackground() {
        // return this.user.isChecked ? "bg-success" : "bg-light";
        // },
        getAlluserList() {
            return this.$store.state.auth.userList;
        }
    },
    watch:{
        getAlluserList(val) {
            console.log(val);
            this.users =val;
            
            this.users=([]);

            val.forEach( (user) => {
                let u = {
                    uid: user.uid,
                    email: user.email,
                    username: user.username,
                    authority: user.authority,
                    creat_at: user.create_at,
                    isChecked: user.authority==1? true: false,
                    isDeleted: false,
                }                
                this.users.push(u);                
            });
        }
    },
    methods: {
        checkUserInfo() {
            this.users.forEach( (user) => {
                console.log(user);
            });
        },
        onChange(user) {            
            // console.log(user.uid);
            user.authority = user.isChecked;
            // console.log('aaa isChecked',user.isChecked);
            // console.log('aaa authority',user.authority);
            this.$store.dispatch("userAuthrity", user);
        },
        myDeleteUser(user) {
            // console.log(user);
            this.$store.dispatch("fireBaseDeleteUser", user);
        },
        // checkTodo() {
        // const todoData = {
        //     id: this.item.id,
        //     currentCheckState: this.item.isChecked,
        // };

        // this.$store.dispatch("checkTodo", todoData);
        // },

        // deleteTodo() {
        // this.$store.dispatch("deleteTodo", this.item.id);
        // },
    },
}



</script>